import {
  Box as MuiBox,
  Button as MuiButton,
  capitalize,
  Grid as MuiGrid,
  TextField as MuiTextField,
} from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useSearchParams } from 'react-router-dom';

import InteractiveTile from '../../components/InteractiveTile/InteractiveTile.component';
import Pagination from '../../components/Pagination/Pagination.component';
import usePagination from '../../hooks/usePagination';
import { selectLoadingFlag } from '../../redux/loading/loading.selectors';
import {
  selectEntities,
  selectPagesTotal,
} from '../../redux/modules/search/search.selectors';
import { fetchSearchResults } from '../../redux/modules/search/search.slice';
import { transformEntityToPlural } from '../../utils';

function render(searchResults) {
  return searchResults.map((searchResult) => {
    const type = searchResult.type;
    const item = searchResult[type];
    let primary, secondary;
    if (type === 'person') {
      primary = item.name;
    } else {
      primary = item.title;
      secondary = item.year;
    }
    return (
      <MuiGrid item key={item.ids.trakt} lg={2} md={3} sm={6} xs={12}>
        <InteractiveTile
          chips={[capitalize(type)]}
          entity={transformEntityToPlural(type)}
          primary={primary}
          secondary={secondary}
          slug={item.ids.slug}
          tmdbId={item.ids.tmdb}
        />
      </MuiGrid>
    );
  });
}
function Search({ entity }) {
  const dispatch = useDispatch();
  const {
    hasNextPage,
    hasPreviousPage,
    page,
    pagesTotal,
    toFirstPage,
    toLastPage,
    toNextPage,
    toPreviousPage,
  } = usePagination(selectPagesTotal);
  const [searchParams, setSearchParams] = useSearchParams();
  const { query = '' } = useMemo(() => Object.fromEntries(searchParams), [
    searchParams,
  ]);
  const fetching = useSelector((state) =>
    selectLoadingFlag(state, 'search/fetch'),
  );
  const searchResults = useSelector(selectEntities);

  useEffect(() => {
    dispatch(fetchSearchResults({ entity, page, query }));
  }, [dispatch, entity, page, query]);

  function handleQueryChange(e) {
    if (e.keyCode !== 13) {
      return;
    }
    setSearchParams({ query: e.target.value });
  }

  return (
    <>
      <MuiBox m={2}>
        <MuiTextField
          color='secondary'
          defaultValue={query}
          label='What are you looking for?'
          onKeyDown={handleQueryChange}
          size='small'
          variant='outlined'
        />
      </MuiBox>
      <MuiGrid container>{render(searchResults)}</MuiGrid>
      <Pagination
        disabled={fetching}
        hasNextPage={hasNextPage}
        hasPreviousPage={hasPreviousPage}
        onFirstPage={toFirstPage}
        onLastPage={toLastPage}
        onNextPage={toNextPage}
        onPreviousPage={toPreviousPage}
        page={page}
        pagesTotal={pagesTotal}
      />
    </>
  );
}

Search.propTypes = {
  entity: PropTypes.oneOf(['all', 'movies', 'shows', 'people']),
};

export default Search;
